import React, { Component } from 'react';
import { connect } from 'react-redux';
import Table from '@material-ui/core/Table';
import EnhancedTablePagination from '../../components/Companies/table/TablePagination/Pagination';
import Paper from '@material-ui/core/Paper';
import EnhancedTableHead from '../../components/Companies/table/tableHeader/Header';
import EnhancedTableBody from '../../components/Companies/table/tableBody/Body';
import Search from '../../components/search/Search';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import customStyle from './Companies.css';
import states from './Companies.state';
import * as actions from '../../store/actions/index';
import Spinner from '../../components/spinner/Spinner';
// function for sorting method
function desc(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function stableSort(array, cmp) {

  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = cmp(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

function getSorting(order, orderBy) {
  return order === 'desc' ? (a, b) => desc(a, b, orderBy) : (a, b) => -desc(a, b, orderBy);
}

// header Data
const rows = [
  { id: 'customerName', numeric: false, disablePadding: false, label: 'Customer Name' },
  { id: 'phone', numeric: false, disablePadding: false, label: 'Phone' },
  { id: 'mobile', numeric: false, disablePadding: false, label: 'Mobile' },
  { id: 'city', numeric: false, disablePadding: false, label: 'City' },
  { id: 'contactName', numeric: true, disablePadding: false, label: 'Contact Name' },
  { id: 'followUp', numeric: false, disablePadding: false, label: 'Follow Up' },
  { id: 'statusText', numeric: false, disablePadding: false, label: 'Status Text' },
  { id: 'lastContactedDate', numeric: true, disablePadding: false, label: 'Last Contacted Date' },
  { id: 'grade', numeric: false, disablePadding: false, label: 'Grade' },
];

// customize theme
const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
});

class Companies extends Component {
  state = states;
  createSortHandler = (property) => event => {

    this.handleRequestSort(event, property);
  };

  
  componentDidMount() {
    this.props.getCompaniesData();
  }


  componentWillReceiveProps(nextProps) {
    if (nextProps.companiesTableData) {
      this.setState({ data: nextProps.companiesTableData })
    }
  }

  // handle sorting request
  handleRequestSort = (event, property) => {
    const orderBy = property;
    let order = 'desc';
    if (this.state.orderBy === property && this.state.order === 'desc') {
      order = 'asc';
    }

    this.setState({ order, orderBy });
  };

  //  table row click handler
  handleTRClick = (event, data) => {

    const { selected } = this.state;
    const selectedIndex = selected.indexOf(data.id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, data.id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));

      this.props.history.replace('./home/companies/companyDetail');
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));

    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );

    }

    this.setState({ selected: newSelected });

    localStorage.setItem('leadDetailRow', JSON.stringify(data));
    this.props.history.replace({
      pathname: '/home/companies/companyDetail',
      search: `?id=${data.id}`,
      state: { detail: data }
    });
  };

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ rowsPerPage: event.target.value });
  };

  isSelected = id => this.state.selected.indexOf(id) !== -1;
  render() {
    // get table data
    if (!this.props.loading && this.props.companiesTableData) {


    }

    const { data, order, orderBy, selected, rowsPerPage, page } = this.state;
    const emptyRows = rowsPerPage - Math.min(rowsPerPage, data.length - page * rowsPerPage);
    const { classes } = this.props;
    let loadingContainer = "";
    if (this.props.loading) {
      loadingContainer = (<Spinner />)
    }

    return (
      <React.Fragment>
        <div className="leadContentHeader">

          <div className={customStyle.spinerWrapper}>
            {loadingContainer}
          </div>
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="flex-end"
          >
            <div className={customStyle.searchSecArea} ><Search color="#ccc" textColor="#111111" /></div>
            <div>
              <button className={[classes.button, "primaryRoundedBtn filterRoundedBtn"].join(' ')}>
                Filter <span className={customStyle.icon}></span>
              </button>
              <button className={[classes.button, "primaryRoundedBtn"].join(' ')}>
                New Customer
</button>

            </div>
          </Grid>


        </div>

        <Paper className={classes.root}>

          <div >
            <Table className={classes.table}>
              <EnhancedTableHead
                rows={rows}
                numSelected={selected.length}
                order={order}
                orderBy={orderBy}
                onSelectAllClick={this.handleSelectAllClick}
                onRequestSort={this.handleRequestSort}
                rowCount={data.length}
                createSortHandler={this.createSortHandler}
              />
              <EnhancedTableBody order={this.state.order} orderBy={this.state.orderBy} handleClick={this.handleTRClick} isSelected={this.isSelected} data={this.state.data} page={this.state.page} rowsPerPage={this.state.rowsPerPage} getSorting={getSorting} emptyRows={emptyRows} stableSort={stableSort} />

            </Table>
          </div>
          <EnhancedTablePagination handleChangeRowsPerPage={this.handleChangeRowsPerPage} handleChangePage={this.handleChangePage} page={this.state.page} rowsPerPage={this.state.rowsPerPage} data={this.state.data} />
        </Paper>
      </React.Fragment>
    );
  }
}


const mapStateToProps = state => {
  return {
    companiesTableData: state.companyReducer.companiesTableData,
    loading: state.companyReducer.loading
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getCompaniesData: () => dispatch(actions.companiesData())
  };
};


export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Companies));

