import React from 'react';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import { withStyles } from '@material-ui/core/styles';
import Moment from 'react-moment';
const CustomTableCell = withStyles(theme => ({
  body: {
    fontSize: 14,
    textAlign: 'left',
    padding: "4px 15px 4px 15px",
  },
}))(TableCell);
const styles = theme => ({

});

function EnhancedTableBody(props) {


  const { classes } = props;

  return (
    <TableBody>
      {props.stableSort(props.data, props.getSorting(props.order, props.orderBy))
        .slice(props.page * props.rowsPerPage, props.page * props.rowsPerPage + props.rowsPerPage)
        .map(n => {


          const isSelected = props.isSelected(n.customerID);

          return (
            <TableRow
              hover
              onClick={event => props.handleClick(event, n)}
              role="checkbox"
              aria-checked={isSelected}
              tabIndex={-1}
              key={n.customerID}
              className={classes.row}
              selected={isSelected}
            >

              <CustomTableCell numeric>{n.customerName || ''}</CustomTableCell>
              <CustomTableCell numeric>{n.ph1 || ''}</CustomTableCell>
              <CustomTableCell numeric>{n.mobile || ''}</CustomTableCell>
              <CustomTableCell numeric>{n.city || ''}</CustomTableCell>
              <CustomTableCell numeric>{n.contactName || ''}</CustomTableCell>
              <CustomTableCell numeric>{n.followUpDate ? <Moment format="MM/DD/YYYY">
                {n.followUpDate}
              </Moment> : '' }</CustomTableCell>
              <CustomTableCell numeric>{n.statusText}</CustomTableCell>
              <CustomTableCell numeric>
                {n.lastContactedDate ? <Moment format="MM/DD/YYYY">
                  {n.lastContactedDate}
                </Moment> : ''}
              </CustomTableCell>
              <CustomTableCell numeric>{n.ratingName}</CustomTableCell>
            </TableRow>
          );
        })}
      {props.emptyRows > 0 && (
        <TableRow style={{ height: 49 * props.emptyRows }}>
          <TableCell colSpan={6} />
        </TableRow>
      )}
    </TableBody>
  );
}

export default withStyles(styles)(EnhancedTableBody);


